/*==========  Desktop First Method  ==========*/

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

html {
  font-size: 10px;
}

.open_rugular {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.open_light {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}

.open_semi {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.raleway_medium {
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

.raleway_semi {
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
}

.raleway_bold {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
}

.nexa {
  font-family: 'NexaFreeBoldwebfont';
}

section#nav {
  background-color: #24282f;
  height: 100px;
  color: #fff;
}

section#nav .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

section#nav .logo {
  margin-top: 35px;
}

section#nav .logo h3 {
  font-family: 'NexaFreeBoldwebfont';
  text-transform: uppercase;
  display: inline-block;
  vertical-align: 3px;
  font-size: 2.6rem;
  margin-left: 10px;
}

section#nav .navigation ul li {
  display: inline-block;
  padding: 20px;
  padding-top: 40px;
  list-style-type: none;
}

section#nav .navigation .active {
  border-bottom: 3px solid #b5a263;
  padding-bottom: 38px;
}

section#nav .navigation a {
  color: #ffffff;
  font-size: 1.4rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  text-decoration: none;
}

section#nav .navigation a:hover,
section#nav .navigation a active,
section#nav .navigation a focuse {
  transition: 0.3s;
}

section#nav .navigation a:hover:after,
section#nav .navigation a active:after,
section#nav .navigation a focuse:after {
  content: "";
  background-color: #b5a263;
  display: block;
  width: 100%;
  height: 3px;
  margin: 38px auto;
  transition: 0.3s;
}

.menu-link {
  z-index: 9;
  width: 50px;
  height: 50px;
  display: block;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4);
  position: fixed;
  right: 20px;
  top: 30px;
}

.menu-link span,
.menu-link span:before,
.menu-link span:after {
  display: block;
  height: 2px;
  width: 18px;
  right: 50%;
  margin-right: -9px;
  background-color: #333;
  position: absolute;
  top: 50%;
  margin-top: -1px;
}

.menu-link span:before {
  transition: all 0.2s;
  content: '';
  margin-top: -6px;
}

.menu-link span:after {
  transition: all 0.2s;
  content: '';
  margin-top: 4px;
}

.menu-link_active span {
  background-color: transparent;
}

.menu-link_active span:before {
  margin-top: 0;
  transform: rotate(135deg);
}

.menu-link_active span:after {
  margin-top: 0;
  transform: rotate(-135deg);
}

.menu {
  opacity: 0.9;
  z-index: 1;
  height: 100vh;
  width: 100vw;
  position: fixed;
  background-color: purple;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  right: -120vw;
  transition: all 0.4s;
}

.menu-active {
  right: 0;
}

.menu ul {
  text-align: center;
  list-style: none;
}

.menu ul li {
  width: 100%;
}

.menu ul a {
  text-decoration: none;
  display: block;
  width: 110vw;
  font-weight: 100;
  letter-spacing: 4px;
  line-height: 4;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 1.3rem;
  color: #fff;
  transition: all 0.2s;
}

.menu ul a:hover {
  transition: all 0.2s;
  background-color: rgba(255, 255, 255, 0.4);
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (min-width: 610px) and (max-width: 3000px) {
  .menu-link {
    display: none;
  }
}

@media (min-width: 0px) and (max-width: 609px) {
  .navigation {
    display: none;
  }
}